@import "variables";

#iawp-parent {
    margin-left: 0;
    margin-right: -10px;

    // Buttons & inputs
    .iawp-button .dashicons, 
    .toplevel_page_independent-analytics #fs_connect .button .dashicons, 
    .toplevel_page_independent-analytics #fs_connect #iawp-parent .button .dashicons {
        margin-right: 0;
        margin-left: 6px;

        &.dashicons-trash,
        &.dashicons-star-filled {
            margin-left: 0;
        }
