        }
    }
    // Toolbar
    .report-title-bar .rename-link h1 {
        margin-right: 0;
        margin-left: 8px;   
    }
    .rename-link .dashicons {
        transform: scale(-1, 1);
    }
    .toolbar .date-picker-parent {
        margin-right: 0;
        margin-left: 12px;
    }
    .modal.large {
        left: auto;
        right: 24px;
    }
    .report-title-bar .buttons:before {
        right: auto;
        left: 100%;
        background: linear-gradient(90deg, $color-purple, transparent);
    }
    .report-title-bar .modal.small {
        right: auto;
        left: 0;
    }
    .report-title-bar .rename-report .modal.small {
