    .modal.downloads {
        left: 24px;
        right: auto;
    }
    // Quick Stats
    .stat .period-label {
        margin-left: 0;
        margin-right: 6px;
    }
    .stat .growth-arrow {
        margin-left: 0;
        margin-right: -3px;
    }
    .stat .count {
