    }
    // Chart
    .chart-interval-select {
        margin-right: auto;
        margin-left: 0;
    }
    .legend-item {
        padding: 4px 8px 4px 12px;
        margin: 0 0 4px 4px;

        span {
            margin-right: 0;
            margin-left: 4px;
        }
    }
    // Table
    .column-picker {
        left: auto;
        right: 0;

        .toggle {
            margin-right: 0;
            margin-left: 6px;

            &.on .indicator {
