    .data-table-container.horizontal .data-table .cell:first-child:not(.hide), 
    .data-table-container.horizontal .data-table .cell.hide:first-child+.cell {
        left: auto;
        right: 0;
        box-shadow: -1px 0 1px rgba(0, 0, 0, .1);
