    .cell-content .dashicons, 
    .cell-content img,
    .cell-content .flag {
        margin-right: 0;
        margin-left: 6px;
    }
    .cell .percentage {
        margin-left: 0;
        margin-right: 4px;
    }
    .cell .deleted-label {
