    }
    .iawp-layout-sidebar .menu-section .report-name {

        a {
            margin-right: 0;
            margin-left: auto;
        }
    }
    .iawp-layout-sidebar .menu-section ol {
        margin: 0 26px 0 0;
    }
    .iawp-layout-sidebar .menu-section .report-name.favorite a:after {
        content: "";
        display: none;
    }
    .iawp-layout-sidebar .menu-section .report-name.favorite a:before {
        content: "\f155";
        color: #f69d0a;
        margin-right: 2px;
        font-family: dashicons;
    }
    .iawp-layout-sidebar .menu-section .icon-container {
        margin-right: 0;
        margin-left: 6px;
    }
    .iawp-layout-sidebar .collapsed-label {
        left: auto;
        right: calc(100% - 6px);
    }
    // Settings 
    .settings-container .tutorial-link {
        margin-left: 0;
        margin-right: auto;

        &:before {
            margin-right: 0;
            margin-left: 6px;
        }
    }
    .pro-tag {
        margin-left: 0;
        margin-right: 12px;
    }
    .email-reports .test-email {
        margin-left: 0;
        margin-right: 12px;
    }
    .blocked-ip-settings .current-ip-status:before {
        margin-right: 0;
        margin-left: 4px;
    }
    .blocked-ip-settings .current-ip {
        margin-left: 0;
        margin-right: 2px;
    }
    form .entry button {
        margin-left: 0;
        margin-right: 8px;
    }
    .settings-container .tutorial-link.absolute {
        right: auto;
        left: 24px;
    }
    // Campaign builder
    .campaign-text {
        margin-right: 0;
        margin-left: 12px;
    }
    .campaign-copy input {
        margin: 0 0 0 16px;
    }
    .campaign-actions .iawp-button {
        margin: 0 0 0 12px;
    }
    .campaign.new .campaign-copy {
        padding-right: 0;
        padding-left: 90px;
    }
    // Other 
    .scroll-to-top {
        right: auto;
        left: 32px;
    }
