// Little triangle pointing into the Analytics menu item
body.analytics_page_independent-analytics-support-center {

    #toplevel_page_independent-analytics .toplevel_page_independent-analytics:after {
        border-right: none;
        border-left-color: $color-purple;
    }
}
@media all and (min-width: 600px) {

    #iawp-parent {
        
        .toolbar[data-filter-count="0"] .download-options-parent {
            margin-right: auto;
            margin-left: 0;
        }
    }
}
// Only apply this for 50/50 stats, so that it doesn't need to be overridden
@media all and (min-width: 650px) and (max-width: 1199px) {

    #iawp-parent {

        .quick-stats {

            .stat:nth-child(odd) {
                border-right: none;
                border-left: solid 1px $color-gray-90;
            }
            &.total-of-3,
            &.total-of-4 {

                .stat:nth-child(3) {
                    border-bottom: none;
                }
            }
            &.total-of-5,
            &.total-of-6 {

                .stat:nth-child(5) {
                    border-bottom: none;
                }
            }
            &.total-of-7,
            &.total-of-8 {

                .stat:nth-child(7) {
                    border-bottom: none;
                }
            }
        }
    }
}
@media all and (min-width: 750px) {
    
    #iawp-parent .legend-item {
        margin: 0 12px 0 0;
    }
}
@media all and (min-width: 700px) {
    
    body.analytics_page_independent-analytics-support-center #iawp-parent {
        
        .support-menu .resource.review {
            margin-right: 0;
            margin-left: 2%;
        }
    }
}
@media all and (min-width: 783px) {

    #iawp-parent {
        margin-left: 0;
        margin-right: -20px;

        .campaign.new {
            left: 0;
            right: 36px;
        }
    }
}
@media all and (min-width: 825px) {

    #iawp-parent {

        .report-title-bar .buttons .favorite {
            padding: 10px 8px 10px 14px;
        }
        #favorite-report-button:not(.active) .dashicons {
            margin-right: 0;
            margin-left: 6px;
        }
    }
}
@media all and (min-width: 961px) {

    #iawp-parent .campaign.new {
        left: 0;
        right: 160px;
    }
}

@media all and (min-width: 1000px) {

    #iawp-parent {

        .iawp-layout-sidebar {
            border-right: none;
            border-left: solid 1px $color-gray-90;
    
            .collapse-sidebar .dashicons {
                transform: rotate(180deg);
            }
        }
        .iawp-layout.collapsed {
                
            .collapse-sidebar .dashicons {
                transform: rotate(0deg);
            }
            .report-inner {
                left: auto;
                right: calc(100% + 1px);
                border-left: solid 1px $color-gray-90;
                border-right: none;
                padding-right: 0;
                padding-left: 18px;
            }
        }
        .iawp-layout.collapsed .collapse-sidebar .dashicons {
            margin: 0;
        }
        .campaign.new {
            left: 0;
            right: 390px;
        }
        .iawp-layout.collapsed {
            
            .campaign.new {
                left: 0;
                right: 202px;
            }   
        }
    } 
}
@media all and (min-width: 1100px) {
    
    #iawp-parent {

        .iawp-layout .toolbar[data-filter-count="1"] .download-options-parent,
        .iawp-layout .toolbar[data-filter-count="2"] .download-options-parent {
            margin-left: 0 !important;
            margin-right: auto;
        }
        // Real-time dashboard
        .real-time-dashboard .most-popular-list:last-child {
            margin-left: auto;
            margin-right: calc(1% + 12px);
        }
    }
}
@media all and (min-width: 1200px) {

    #iawp-parent {

        .toolbar[data-filter-count="2"] .download-options-parent {
            margin-right: auto;
            margin-left: 0;
        }
        .quick-stats {

            .stat {
                width: 25%;
                border-right: none;
                border-left: solid 1px $color-gray-90;
    
                &:last-child {
                    border-left: none;
                }
            }
            &.total-of-5,
            &.total-of-6 {
    
                .stat {
                    width: 33.33%;
    
                    &:nth-child(3) {
                        border-left: none;
                    }
                    &:nth-child(4),
                    &:nth-child(5) {
                        border-bottom: none;
                    }
                }
            }
            // TODO: rewrite with mathematical formula e.g. nth-child(4n-1)
            &.total-of-7,
            &.total-of-8 {
    
                .stat:nth-child(5),
                .stat:nth-child(6),
                .stat:nth-child(7) {
                    border-bottom: none;
                }
                .stat:nth-child(4) {
                    border-left: none;
                }
            }
        }
    }
}
@media all and (min-width: 1400px) {

    #iawp-parent {
    
        .toolbar .modal.large {
            left: auto;
            right: 0;
        }
    }
}
